@font-face {
  font-family: lcd;
  src: url(./fonts/lcd\ phone\ Regular.ttf);
}

* {
  box-sizing: border-box;
  color: rgb(70,210,210);
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-color: rgb(100,100,110);
}

#calc-container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 30vw;
  height: 80vh;
  background-color: rgb(40,40,60);
  border-radius: 5px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  margin: auto;
}

#display-container {
  justify-content: space-around;
  align-items: flex-end;
  width: 100%;
  height: 20%;
}

#display-area {
  word-wrap: break-word;
  word-break: break-all;
  box-shadow: inset 0px 0px 3px turquoise;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  border-radius: 3px;
  padding: 10px;
  margin: 15px;
  background-color: rgb(80,80,100);
}

#formula {
  font-family: lcd;
  padding: 7px 0 20px 0;
  margin: 0px;
  height: auto;
}

#display {
  font-family: lcd;
  padding: 2px;
  margin: 0px;
  height: auto;
}

#buttons-container {
  padding: 15px;
  width: 100%;
  height: 80%;
  display: grid;
  grid-template-areas: "-clear -clear -divide -multiply" "-one -two -three -minus" "-four -five -six -plus" "-seven -eight -nine -equals" "-zero -zero -point -equals";
}


.button {
  /* all: initial; */
  border-radius: 6px;
  background-color: transparent;
  width: 100%;
  height: 100%;
  text-align: center; 
}

.button:hover {
  border: 2px solid turquoise;
}

.button-active {
  border-radius: 6px;
  color: rgb(40,40,60);
  background-color: rgb(70,210,210);
  width: 100%;
  height: 100%;
  text-align: center; 
}


#clear {
  margin: 2px;
  grid-area: -clear;
}

#divide {
  margin: 2px;
  grid-area: -divide;
}

